
import { defineComponent } from 'vue';
import {Post} from '../mocks'

type Period = 'Today' | 'This Week' | 'This Month'

export default defineComponent({
  name: 'TimelinePost',
  props: {
    post: {
      type: Object as () => Post,
      required: true
    }
  },

  setup(props) {
    return {
      to: `/posts/${props.post.id}`
    }
  }
});
